:root {
  --color-primary: #c5a46d;
  --color-text-dark: #2a2a2a;
  --color-text-light: #666666;
  --color-background: #ffffff;
  --color-background-alt: #eaeaea;
  --color-background-alt-2: #f8f4f0;
  --color-background-alt-3: #eae5e5;
  --color-footer: #25384d;
  --color-dark: #000000;
  --font-heading: 'Playfair Display', serif;
  --font-body: 'Poppins', sans-serif;
  --font-special: 'Cinzel', serif;
  --font-nav: 'Poiret One', sans-serif;
  --primary-bg: #f8f4f0;
  --secondary-bg: #ffffff;
  --footer-bg: #25384d;
  --text-dark: #2a2a2a;
  --text-light: #ffffff;
  --brand-accent: #c5a46d;
  --button-bg: #25334d;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body {
  max-width: 100vw;
  overflow-x: hidden;
}

body {
  font-family: var(--font-body);
  color: var(--color-text-light);
  background-color: var(--color-background);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

.page-wrapper {
  max-width: 1280px;
  margin: 0 auto;
  overflow: hidden; /* To contain absolutely positioned elements */
}

.highlight {
  color: var(--color-primary);
}

@media (max-width: 768px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
}

